import { EditarPage } from './editar/editar.page';
import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { NuevoPage } from './nuevo/nuevo.page';

export interface Gasto {
  image?: string;
  proveedor: string;
  fecha: string;
  monto: number;
  documento: number;
  rut: string;
  tipodoc: number;
  categoria_id: number;
  categoria: string;
  nota?: string;
}

export const Gastos: Gasto[] = [{
  proveedor: 'Proveedor 1',
  fecha: '01/08/2019',
  monto: 10000,
  documento: 100001,
  rut: '11.111.111-1',
  tipodoc: 1,
  categoria_id: 1,
  categoria: 'Alimentacion'
}, {
  proveedor: 'Proveedor 2',
  fecha: '01/08/2019',
  image: 'no_image',
  monto: 20000,
  documento: 100001,
  rut: '11.111.111-1',
  tipodoc: 1,
  categoria: 'Combustible',
  categoria_id: 2
}];

@Component({
  selector: 'app-gastos',
  templateUrl: './gastos.page.html',
  styleUrls: ['./gastos.page.scss'],
})
export class GastosPage implements OnInit {
  gastos;

  constructor(public modalController: ModalController) { }

  ngOnInit() {
    this.gastos = Gastos;
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: NuevoPage,
      animated: true,
      showBackdrop: true,
      mode: 'ios',
    });
    return await modal.present();
  }

  async modalEditar(item) {
    const modal = await this.modalController.create({
      component: EditarPage,
      componentProps: item,
      mode: 'ios',
      showBackdrop: false
    });
    return await modal.present();
  }


}
